@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Wrapper
{
  height: 100%;
  width: 100%;
}

.ArrowWrapper
{
  position: relative;
  overflow-x: hidden;
}

.Nav-Image
{
  object-fit: cover;
  height: 100%;
  width: 100%;
}

.Nav-Image-Container
{
  position: relative;
  display: flex;
  flex-wrap: nowrap;
  overflow-x: visible;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar;
  height: 100%;
}

.Nav-Image-Item-Container
{
  position: relative;
  height: 100%;
}

.Nav-Image-Link
{
  height: 100%;
  width: 100%;
  flex: 0 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden
}

.Nav-Image-Link .Nav-Image-Label
{
  position: absolute;
  left: 0;
  bottom: 0;

  background-color: rgba(0, 0, 0, 0.5);;
  opacity: 0;

  size: 5vmin;
  width: 100%;
  padding: 1.5vmin 0;
  text-align: center;
  color: white;
}

.Nav-Image:hover + .Nav-Image-Label
{
  opacity: 1;
}

.LeftArrow
{
  position: absolute;
  left: 1em;
  top: 50%;
  transform: translateY(-50%) scaleX(-1);
}

.RightArrow
{
  position: absolute;
  right: 1em;
  top: 50%;
  transform: translateY(-50%);
}
.App {
  text-align: center;
  overflow-x: hidden;
}

.App-header {
  background-color: #B4A96A;
  background-image: linear-gradient(to bottom right, #B4A96A, #8E753D);
  height: 90vh;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: space-evenly;
  font-size: calc(10px + 2vmin);
  font-family: 'Source Sans Pro', sans-serif;
  color: white;
  overflow-x: hidden;
}

.App-link {
  color: #61dafb;
}

h1
{
  text-shadow: 2px 2px #6A4E0D;
  margin: 0;
}

p
{
  margin: 2rem 0 0 0;
}

.SliderContainer
{
  align-items: center;
}

.ContentContainer
{
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  overflow-x: hidden;
}

.TagText
{
  flex-basis: 100%;
}

.TextBox
{
  display: flex;
  flex-direction: column;
  padding: 1rem;
  margin: 2rem;
  background-color: #f0f0f0;
  text-align: left;
  width: 40%;
  min-width: 15rem;
}

.CodeBox {
  padding: 1rem;
  margin: 2rem;
  border:1px solid gray;
  background-color:#f0f0f0;
  width:40%;
  text-align: left;
  overflow:auto;    
  padding:10px;
}
.CodeBox code {
  font-family: Courier, sans-serif;
  font-size: 1em;
}

.Footer
{
  min-height: 1rem;
  background-color: #6A4E0D;
  padding: 1rem;
  color: white;
}

ul
{
  list-style-type: none;
  display: inline;
  margin: 1em 0;
  padding: 0;
}

a
{
  text-decoration: none;
  color: #FFFCE8;
}
